@media only screen and (max-width: 768px) {
  .services {
    padding: 1rem;
  }

  .detailed-topics {
    margin: 2rem 0rem !important;
  }

  .detailed-topics .topic {
    display: block !important;
  }

  .detailed-topics .topic .img-container {
    width: 100% !important;
    text-align: center !important;
  }

  .detailed-topics .topic .img-container img {
    width: 100% !important;
    max-width: 550px !important;
    transform: scale(1) !important;
}

  .detailed-topics .topic .details-container {
      width: 100% !important;
      text-align: center !important;
  }

  .main-topics .topic .icon {
      width: 60px !important;
      height: 60px !important;
  }

  .main-topics .topic .icon::after {
    width: 60px !important;
    height: 60px !important;
}

.main-topics .topic .topic-name {
    margin-top: 1rem !important;
    font-size: .9rem !important;
  }

}

.services {
    max-width: 1240px;
    margin: auto;
    margin-top: 5rem;
    padding: 1rem;
  }
  

.main-topics {
  display: flex;
  flex-wrap: wrap;
}

.main-topics .topic {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.main-topics .topic .icon {
  width: 92px;
  height: 92px;
  border-radius: 50%;
  border: 2px solid black;
  position: relative;
}

.main-topics .topic .icon:hover img {
  top: calc(50% - 22px);
}

.main-topics .topic .icon::after {
  content: "";
  position: absolute;
  width: 92px;
  height: 92px;
  border-radius: 50%;
  top: 12px;
  left: 12px;
  background-color: rgba(36, 227, 148, 1);
  z-index: -1;
}

.main-topics .topic .icon img {
  width: 28px;
  height: 28px;
  position: absolute;
  top: calc(50% - 14px);
  left: calc(50% - 14px);
  transition: top 0.1s ease-in;
}

.main-topics .topic .topic-name {
  margin-top: 4rem;
  font-size: 2rem;
}

.container {
  max-width: 1040px;
  text-align: center;
  margin: 2rem 0rem;
  margin: auto;
  padding: 2rem 0rem;
}

.detailed-topics {
  margin: 8rem 0rem;
}

.detailed-topics .topic {
  margin: 2rem 0rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.detailed-topics .topic .img-container {
  width: 50%;
  text-align: left;
  order: 1;
}
.detailed-topics .topic .details-container {
  width: 50%;
  text-align: left;
  order: 2;
}

.detailed-topics .topic:nth-child(2) .img-container {
  order: 2;
  text-align: right;
}
.detailed-topics .topic:nth-child(4) .img-container {
  order: 2;
  text-align: right;
}

.detailed-topics .topic:nth-child(2) .details-container {
  order: 1;
}
.detailed-topics .topic:nth-child(4) .details-container {
  order: 1;
}

.detailed-topics .topic:nth-child(2) .details-container .title,
.detailed-topics .topic:nth-child(2) .details-container .description {
  padding-left: 0px !important;
  padding-right: 1rem;
}

.detailed-topics .topic .img-container img {
  max-height: 300px;
  transition: transform 0.4s ease-out;
}

.detailed-topics .topic:hover .img-container img {
  max-height: 300px;
  transform: scale(1.15);
}

.detailed-topics .topic .details-container {
  padding-top: 2rem;
}

.detailed-topics .topic .details-container .title {
  font-size: 2rem;
  margin-bottom: 2rem;
  padding-left: 1rem;
}

.detailed-topics .topic .details-container .description {
  padding-left: 1rem;
}
