@media only screen and (max-width: 768px) {
  .technology-icon {
    width: 33.33% !important;
  }

  .technology-icon img { 
      width: 44px !important;
  }

  .tech-name {
    margin: 0.5rem !important;
    font-size: 0.8rem !important;
  }
}

.technology-list {
  padding: 3rem 0rem;
  display: flex;
  flex-wrap: wrap;
}

.technology-icon {
  margin-bottom: 1.2rem;
  width: 20%;
}

.technology-icon img {
  cursor: pointer;
  width: 48px;
  filter: grayscale(1);
  transition: filter 0.3s, transform 0.3s;
}

.technology-icon img:hover {
  filter: grayscale(0);
  transform: translateY(-10px);
}

.tech-name {
  transition: opacity 0.3s;
  opacity: 0;
  font-size: 0.9rem;
  margin: 1rem;
}

.technology-icon img:hover + .tech-name {
  opacity: 1;
}

.error {
  box-shadow: 0px 0px 20px #eee;
  text-align: center;
  font-size: 1.3rem;
  margin: auto;
  padding: 4rem;
  border-radius: 8px;
}
