@media only screen and (max-width: 768px) { 
    .hi-there .intro{
        margin-top: 1rem !important;
        text-align: center !important;
        display: block !important;
    }

    .hi-there .intro .hi {
        width: 100% !important;
        margin: 1rem 0rem;

    } 
    .hi-there .intro .description {
        width: 100% !important;
        font-size: 1rem !important;
    }
}

.hi-there .intro{
    margin-top: 4rem;
    width: 100%;
    text-align: left;
    display: flex;
    color: #0a0a0a;
}

.hi-there .intro .hi{
    width: 50%;
    position: relative;
}

.hi-there .intro .description{
    line-height: 1.6;
    width: 50%;
    position: relative;
}